import React from 'react';
import styled from '@emotion/styled';
import { css } from '@emotion/core';
import { Link, graphql, useStaticQuery } from 'gatsby';
import BackgroundImage from 'gatsby-background-image';
import Image from 'gatsby-image';

const ImageBackground = styled(BackgroundImage)`
  background-position: top 20% center;
  background-size: cover;
  height: 50vh;

  + * {
    margin-top: 0rem;
  }
`;

const TextBox = styled('div')`
  background-image: linear-gradient(to top, #ddbbffdd 2rem, #ddbbff00);
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: flex-end;
  padding: 0 calc((100vw - 675px) / 2) 2rem;
  margin-top: 0;
  width: 100%;

  h1 {
    text-shadow: 1px 1px 3px #eeddff66;
    font-size: 2.25rem;
    margin-left: 1rem;
    font-family: Palatino, Palatino Linotype, Palatino LT STD, Book Antiqua, Georgia, serif;
  }

  P,
  a {
    color: #222;
    margin-top: 0;
  }

  a {
    margin-top: 0.5rem;
  }

  @media (max-height: 470px) {
    padding-bottom: 0.5rem;
    font-size: 0.8rem;
    p a { display: none; }
  }


`;

const Hero = () => {
  const { bgimage, logo } = useStaticQuery(graphql`
    query {
      bgimage: file(relativePath: { eq: "roberto-nickson-skyline.jpg" }) {
        sharp: childImageSharp {
          fluid {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      logo: file(relativePath: { eq: "nwwnc-2019-logo-hi-res-cyan.png" }) {
        sharp: childImageSharp {
          fluid(maxWidth: 360, maxHeight: 360) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
    }
  `);

  return (
    <ImageBackground Tag="section" fluid={bgimage.sharp.fluid} fadeIn>
      <TextBox>
        <div css={css`
          display: flex;
          align-items: center;
          margin-bottom: 1rem;
          padding: 0 0.5rem;

          @media (max-height: 470px) {
            justify-content: center;
          }
        `}>
          <Image
            fluid={logo.sharp.fluid}
            css={css`
              * {
                margin-top: 0;
              }
              width: 120px;
              @media (max-height: 470px) {
                width: 80px;
              }
            `}
            alt="North Westwood Neighborhood Council"
          />
          <h1>NWWNC</h1>
        </div>
        <p css={css`padding: 0 0.5rem`}>
          The North Westwood Neighborhood Council serves its residents, Westwood Village and UCLA. Latest Meeting News below, or <Link to="/meetings">Browse Meetings Index &rarr;</Link>
        </p>
      </TextBox>
    </ImageBackground>
  );
};

export default Hero;
